body {
  background-color: black;
}

h1 {
  margin: 200px 0 0 0;
  padding: 0 0 80px 0;
  font-size: 3rem;
  text-align: center;
  color: white;
}

.FormHolder{
  padding: 300px 0 0 0;
  color: white;
  font-size: 2rem;
  justify-content: center;
  text-align: center;
}

.Previous {
  margin: 0 10px 0 40px;
  font-size: 2rem;
}

.Next {
  margin: 0 0 0 0;
  font-size: 2rem;
}

.Submit {
  margin: 0 0 0 0;
  font-size: 2rem;
}

.Bar {
  width: 50%;     /* Adjust as needed */
  padding: 15px;   /* Adjust for bigger padding */
  font-size: 1.8rem; /* Adjust for bigger font size */
  border: 1px solid #757575;
  border-radius: 5px; /* Optional: for rounded corners */
  background-color: #757575;
}
